import React from 'react';
import {  Container } from '@mui/material';
import {
  VictoryChart,
  VictoryTheme,
  VictoryLine,
  createContainer,
  VictoryAxis,
  VictoryLabel,
  VictoryBar,
  VictoryVoronoiContainer,
  VictoryTooltip,
} from "victory";

import { lineData, variationData } from './data';

const VictoryZoomVoronoiContainer = createContainer("zoom", "voronoi");

function wc_hex_is_light(color) {
  const hex = color.replace('#', '');
  const c_r = parseInt(hex.substring(0, 0 + 2), 16);
  const c_g = parseInt(hex.substring(2, 2 + 2), 16);
  const c_b = parseInt(hex.substring(4, 4 + 2), 16);
  const brightness = ((c_r * 299) + (c_g * 587) + (c_b * 114)) / 1000;
  return brightness > 155;
}

class ArrowLine extends React.Component {
  render() {
    const { x, y, y0 } = this.props;
    return (
      <svg>
        <defs>
        <marker
          id="arrow"
          viewBox="0 0 10 10"
          refX="5"
          refY="5"
          markerWidth="6"
          markerHeight="6"
          orient="auto-start-reverse">
          <path d="M 0 0 L 10 5 L 0 10 z" />
        </marker>
      </defs>

      <line
        x1={x}
        y1={y0}
        x2={x}
        y2={y}
        stroke="black"
        markerEnd="url(#arrow)" />
      </svg>
    );
  }
}

const Graphs = () => {
  return (
    <Container
      maxWidth="xl"
      sx={{
        paddingY: (theme) => [ theme.spacing(4), theme.spacing(8) ]
      }}
    >
     <VictoryChart
        width={1200}
        height={500}
        theme={VictoryTheme.material}
        padding={{ left: 100, bottom: 50, top: 50, right: 25 }}
        containerComponent={
          <VictoryZoomVoronoiContainer 
            labels={({ datum }) => `${datum.name} \n ${+(Math.round(datum.y + "e+2")  + "e-2")}`}
            labelComponent={
              <VictoryTooltip
                flyoutStyle={{ 
                  fill: ({ datum }) => datum.y0 ? '#ffffff' : datum.color,
                }}
                style={{
                  fill: ({ datum }) => datum.color && wc_hex_is_light(datum.color) ? '#000000' : (datum.color ? '#FFFFFF' : '#000000'),
                }}
              />
            }
          />
        }
      >
        {lineData.map((line) => (
          <VictoryLine
            key={line[0].color}
            style={{
              data: {
                stroke: line[0].color,
                strokeWidth: ({ active }) => (active ? 4 : 1)
              }
            }}
            data={line.map((d) => ({ x: d.period, y: d.value, name: d.name, color: d.color }))}
          />
        ))}
        <VictoryAxis crossAxis
          label="Simulation period"
          theme={VictoryTheme.material}
          standalone={false}
          offsetY={50}
        />
        <VictoryAxis dependentAxis crossAxis
          label="Indicator of progress"
          theme={VictoryTheme.material}
          axisLabelComponent={<VictoryLabel dy={-40} />}
          padding={{ left: 1000 }}
          standalone={false}
        />
      </VictoryChart>
      <VictoryChart
        width={1200}
        height={600}
        containerComponent={
          <VictoryVoronoiContainer
            labels={({ datum }) => `${datum.x} \n ${+(Math.round(datum.y + "e+2")  + "e-2")}`}
            labelComponent={
              <VictoryTooltip
                flyoutStyle={{ 
                  fill: ({ datum }) => datum.y0 ? '#ffffff' : datum.color,
                }}
                style={{
                  fill: ({ datum }) => datum.color && wc_hex_is_light(datum.color) ? '#000000' : (datum.color ? '#FFFFFF' : '#000000'),
                }}
              />
            }
          />
        }
        padding={{ left: 100, bottom: 105, right: 25, top: 50 }}
        domainPadding={{ x: 10 }}
      >
        <VictoryBar
          style={{
            data: {
              fill: ({ datum }) => datum.color,
            }
          }}
          data={variationData.map((data) => ({ x: data.name, y: data.value, color: data.color }))}
        />
        <VictoryBar
          data={variationData.map((data) => ({ x: data.name, y: data.valueNext, y0: data.value }))}
          dataComponent={<ArrowLine/>}
        />
        <VictoryAxis
          label="Indicators"
          axisLabelComponent={<VictoryLabel dy={50} />}
          tickLabelComponent={<VictoryLabel angle={90} dx={-5} dy={-5}  textAnchor="start" style={{ fontSize: 9 }}/>}
        />
        <VictoryAxis dependentAxis crossAxis
          label="Indicator progress change"
          axisLabelComponent={<VictoryLabel dy={-40} />}
          standalone={false}
        />
      </VictoryChart>
    </Container>
  )
}

export { Graphs };
